import React, { Component } from 'react';
import '../css/Projects.css';
import githubEn from '../assets/github_engraved.png';
import go from '../assets/go.png';

class Projects extends Component {
  constructor() {
    super()
    this.state = { thumbImg: null}
  }

  toggleThumb = (e) => {
    let tileId = e.target.id;
    let { info } = this.props;
    let thumb = tileId ? info[tileId].thumbnail : null
    this.setState({
      thumbImg: thumb
    })
  }
  clearThumb = () => {
    this.setState({
      thumbImg: null
    })
  }
  render() {
    let { info } = this.props;
    let { thumbImg } = this.state;

    let colors = ["#3FDE9A","#FA2D57","#FD9900","#167AFF","#000000","#FFFFFF","#FDCC01"]
    let projectList = info.map((el,i) => {
      return ((i !== 4)
              ? <div key={i} className="wrapper">
                  <div  className="gray-effect"
                        onMouseEnter={this.toggleThumb}
                        onMouseLeave={this.clearThumb}
                        id={i}>
                    <div className="links">
                      {  el.liveUrl ? <a id={i} href={el.liveUrl} target="_blank" rel="noopener noreferrer"><img src={go} alt="source"></img></a> : null}
                      <a id={i} href={el.gitUrl} target="_blank" rel="noopener noreferrer"><img src={githubEn} alt="source"></img></a>
                    </div>
                  </div>
                  <div
                    className="project-item"
                    style={{"background":`${colors[Math.round(i % colors.length)]}`}}
                  ><h3>{el.title}</h3>
                    <p id="project">{el.description}</p>
                  </div>
                </div>
              : <div key={i} className="thumbnail">{thumbImg ? <img src={thumbImg} alt="thumb" /> : null}</div>)
    })
    return (
      <React.Fragment>
        <div className="projects">
          <h1>| My Projects</h1>
          <div className="project-list">
          {projectList}
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default Projects;
